        grid-row-gap: 10%;
        text-align: center;
    }

    .footer .w-footer-list .w-social .phone {
        display: inline-block;
    }

    a.dgt-btn.dgt-login {
        left: 50%;
        transform: translate(-50%);
    }
}

@media screen and (max-width: 480px) {
    .footer .w-footer-list {
        padding: 150px 20px;
    }

    .deposit-bank-boxes {
        grid-template-columns: 1fr;
    }
}

.perks-choose-donation .exchange-donate .exchange-result.exchangeNotOK {
    visibility: initial;
    position: relative;
    margin-top: 0;
    margin-bottom: 80px;
}

.perks-choose-donation .exchange-donate .exchange-result.exchangeNotOK a {
    text-decoration: underline;
}

#submit_perks_form {
    display: none;
}
.clearfix {
    overflow: auto;
}

.clearfix::after {
    content: "";
    clear: both;
    display: table;
}

.hidden, [hidden], [x-cloak], [v-cloak] {
    display: none !important;
}

.text-left {
    text-align: left;
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

.special-request-error {
    color: #ff0000;
}

.special-request-columns {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 16px;
}

@media (max-width: 768px) {
    .special-request-columns {
        grid-template-columns: 1fr;
    }
}

.form .form-row {
    margin-bottom: 16px;
}

.form .form-label {
    display: block;
}

.form .form-button {
    background: transparent none repeat scroll 0 0;
    text-decoration: none;
    border: 1px solid;
    font-size: 18px;
    margin-top: 10px;
    padding: 10px 25px;
}

.form .form-button:hover {
    background: #009AD6;
    color: #fff;
}

.form .form-label.required:before {
    display: inline-block;
    content: '*';
    color: #ff0000;
    margin-right: 5px;
}

.form .form-input,
.form .form-select {
    display: block;
    width: 100%;
    padding: 8px 6px;
    background-color: white;
    color: #333;
    border: solid 1px #333;
    border-radius: 0;
}

.form .form-error {
    display: block;
    color: #ff0000;
    color: #ff0000;
}
